import { Metadata } from "next";

export function getServerMetadata(locale: string = "fr"): Metadata {
  return {
    title:
      locale === "en"
        ? "Techtrust - Digital agency"
        : "Techtrust - Agence digitale",
    description:
      locale === "en"
        ? "Digital agency specialized in web creation and growth hacking."
        : "Techtrust – Agence digitale web & growth.",
  };
}
